.content-tab {
    position: relative;
    display: block;
    width: 23%;
    margin: 1%;
    border: 3px solid black;
    border-bottom: 1px solid black;
    box-sizing: border-box;
}

.content-tab > .content-tab-image {
    width: 100%;
    border-bottom: 1px solid black;
}

.content-tab > .content-tab-title {
    position: absolute;
    top: 0%;
    left: 5%;
}

.content-tab > .content {
    padding: 2%;
    box-sizing: border-box;
}

.content-tab > .content > .content-tab-tags {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: flex-start;
}

.content-tab > .content > .content-tab-tags > * {
    padding: 1%;
    margin: 1%;
    background-color: gray;
    border: white;
}